.component-button {
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 25%;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}

.component-button.wide {
  width: 50%;
}

.component-button button {
  background-color: #e0e0e0;
  border: 0;
  font-size: 1rem;
  font-weight: 200;
  margin: 0 1px 0 0;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  padding: 15px;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.component-button.active button {
  -webkit-animation: InOutWhite 0.2s;
          animation: InOutWhite 0.2s;
}

.component-button.active.orange button {
  -webkit-animation: InOutOrange 0.2s;
          animation: InOutOrange 0.2s;
}

.component-button button:active {
  background-color: #f9f9f9;
}

.component-button button:focus {
  outline: none;
}

.component-button:last-child button {
  margin-right: 0;
}

.component-button.orange button {
  background-color: #f5923e;

  color: white;
}

.component-button.orange button:active {
  background-color: #f7a45e;
}

.component-button.component-button.orange button:focus {
  outline: none;
}



