.btn-undo {
  width: 1.1rem;
  height: 1.1rem;
  border-radius: 100px;
  border: 1px solid #eee;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-left: 0.5rem;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.btn-undo:active {
  background-color: #e3e3e3;
}

.btn-undo:focus {
  outline: none;
}

.btn-undo img {
  width: 70%;
}



